.section-title {
  font-family: Lineatura, sans-serif;
  font-weight: 700;
  font-size: 24px;
  line-height: 150%;
  text-align: center;
  text-transform: uppercase;
  text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4);
}
.section-title span {
  color: rgb(88, 220, 227);
}
@media (min-width: 421px) {
  .section-title {
    font-size: 26px;
  }
}
@media (min-width: 1024px) {
  .section-title {
    font-size: 40px;
    line-height: 140%;
  }
}
@media (min-width: 1401px) {
  .section-title {
    font-size: 50px;
  }
}

.btn-row {
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-style {
  display: inline-block;
  background-color: #09D3C6;
  font-family: Exo2, sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
  text-transform: uppercase;
  box-shadow: 0px 8px 15px rgba(9, 211, 198, 0.55);
  border-radius: 10px;
  padding: 18px 40px;
  transition: background-color 0.3s, box-shadow 0.3s;
  border: none;
}
@media (min-width: 1024px) {
  .btn-style {
    font-size: 16px;
    padding: 22px 40px;
  }
}
@media (min-width: 1441px) {
  .btn-style {
    min-width: 210px;
    text-align: center;
  }
}
.btn-style:hover {
  background-color: #09D335;
  box-shadow: 0px 8px 15px rgba(9, 211, 53, 0.55);
}

.input-style {
  display: block;
  width: 100%;
  background-color: transparent;
  border: 1px solid #D5D5D5;
  border-radius: 50px;
  font-size: 12px;
  line-height: 150%;
  padding: 13px 22px 14px;
  transition: border-color 0.3s;
  color: #fff;
}
@media (min-width: 1024px) {
  .input-style {
    font-size: 14px;
  }
}
@media (min-width: 1441px) {
  .input-style {
    font-size: 16px;
    padding: 16px 30px 16px;
  }
}
.input-style:focus {
  border-color: rgb(88, 220, 227);
}
.input-style.placeholder {
  color: #fff;
}
.input-style::-webkit-input-placeholder {
  color: #fff;
}
.input-style::-moz-placeholder {
  color: #fff;
}
.input-style:-moz-placeholder {
  color: #fff;
}
.input-style:-ms-input-placeholder {
  color: #fff;
}

.file-group label {
  position: relative;
}
.file-group label .label {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 0 5px;
}
.file-group label .label .icon-file {
  display: inline-block;
  color: rgb(88, 220, 227);
  font-size: 20px;
  line-height: 20px;
  margin-right: 15px;
}
.file-group label .label span {
  font-size: 12px;
  line-height: 150%;
  text-decoration: underline;
}
@media (min-width: 1024px) {
  .file-group label .label span {
    font-size: 14px;
  }
}
@media (min-width: 1441px) {
  .file-group label .label span {
    font-size: 16px;
  }
}
.file-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.file-group label:hover .label span {
  text-decoration: none;
}

.radio-group label {
  display: block;
  position: relative;
}
.radio-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.radio-group label input:checked + .label {
  border-color: rgb(88, 220, 227);
}
.radio-group label input:checked + .label:before {
  border-color: rgb(88, 220, 227);
}
.radio-group label .label {
  display: block;
  width: 100%;
  background-color: transparent;
  border: 1px solid #D5D5D5;
  border-radius: 50px;
  font-size: 12px;
  line-height: 150%;
  padding: 13px 22px 14px 44px;
  transition: border-color 0.3s;
  color: #fff;
  position: relative;
}
@media (min-width: 1024px) {
  .radio-group label .label {
    font-size: 14px;
  }
}
@media (min-width: 1441px) {
  .radio-group label .label {
    font-size: 16px;
    padding: 16px 22px 17px 55px;
  }
}
.radio-group label .label:before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  background-color: #fff;
  border-radius: 50%;
  position: absolute;
  top: 15px;
  left: 15px;
  border: 4px solid transparent;
  transition: border-color 0.3s;
}
@media (min-width: 1441px) {
  .radio-group label .label:before {
    width: 21px;
    height: 21px;
    top: 18px;
    left: 18px;
  }
}

.check-group label {
  display: block;
  position: relative;
  text-align: left;
}
.check-group label input {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 0;
  opacity: 0;
  visibility: hidden;
}
.check-group label input:checked + .label:before {
  border-color: rgb(88, 220, 227);
  color: rgb(88, 220, 227);
}
.check-group label .label {
  display: inline-block;
  padding-left: 42px;
  font-weight: 400;
  font-size: 10px;
  line-height: 150%;
  position: relative;
}
@media (min-width: 1024px) {
  .check-group label .label {
    font-size: 12px;
  }
}
@media (min-width: 1441px) {
  .check-group label .label {
    font-size: 14px;
  }
}
.check-group label .label:before {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: Fontello;
  font-style: normal;
  font-weight: normal;
  speak: never;
  margin: 0;
  text-decoration: inherit;
  text-align: center;
  font-variant: normal;
  text-transform: none;
  content: "\e802";
  color: transparent;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  border: 1px solid #fff;
  font-size: 8px;
  line-height: 8px;
  position: absolute;
  top: -1px;
  left: 5px;
  transition: color 0.3s, border-color 0.3s;
}
@media (min-width: 1024px) {
  .check-group label .label:before {
    top: 1px;
  }
}
.check-group label .label a:hover {
  text-decoration: none;
}

