@import "settings"
@import "_mixins/_mixins"
@import "_components/ui"

// main products
.main-products
  &__title
    max-width: 340px
    margin-top: 6.75vh
    @media (min-width: 768px)
      margin-top: 14.25vh
    @media (min-width: 1024px)
      margin-top: 11.25vh
      max-width: 620px
    @media (min-width: 1441px)
      margin-top: 8.25vh
      max-width: 945px
    @media (min-width: 1200px)
      @media (max-height: 1000px)
        margin-top: 2.25vh
  .section-content
    &:before
      content: ""
      display: block
      width: calc(100% + 30px)
      height: 600px
      position: absolute
      bottom: calc(100% - 285px)
      left: 0
      background-image: url(../images/common/ancors/clouds.png)
      background-repeat: no-repeat
      background-size: cover
      background-position: calc(45% - 15px) 0
      animation: fly_cloud 6s ease 0s infinite normal forwards
      pointer-events: none
      @media (min-width: 1441px)
        background-size: contain
        background-position: 0 0
        bottom: calc(100% - 425px)
        height: 700px
        left: 10%
  .products-wrapper
    flex: 1 1 auto
    height: 100%
    width: 100%
    position: relative
    @media (min-width: 768px)
      max-width: 350px
      margin: 0 auto
    @media (min-width: 1024px)
      max-width: 950px
    @media (min-width: 1441px)
      max-width: 1835px
    img
      display: block
      width: auto
      position: absolute
    .product_image1
      top: 14%
      left: 14%
      max-width: 84px
      @media (min-width: 768px)
        top: 13.5%
        left: -16.5%
        max-width: 105px
      @media (min-width: 1024px)
        top: 32.5%
        left: 13.5%
        max-width: 155px
      @media (min-width: 1441px)
        top: 24.5%
        left: 22.5%
        max-width: 100%
    .product_image2
      top: 4.5%
      right: 15%
      max-width: 110px
      @media (min-width: 768px)
        top: 2%
        right: -10%
        max-width: 125px
      @media (min-width: 1024px)
        top: 25%
        right: 26%
        max-width: 110px
      @media (min-width: 1441px)
        max-width: 100%
        top: 13%
        right: 31.5%
    .product_image3
      top: 51%
      left: 5%
      max-width: 80px
      @media (min-width: 768px)
        top: 60%
        left: -28%
        max-width: 95px
      @media (min-width: 1024px)
        top: 72%
        left: 16%
        max-width: 100px
      @media (min-width: 1441px)
        top: 82.5%
        left: 24%
        max-width: 100%
    .product_image4
      top: 50.5%
      right: 8%
      max-width: 110px
      @media (min-width: 768px)
        top: 60.5%
        right: -12%
        max-width: 125px
      @media (min-width: 1024px)
        top: 52.5%
        right: 19%
        max-width: 135px
      @media (min-width: 1441px)
        max-width: 100%
        top: 54%
        right: 26%
    .product_image5
      top: 87.5%
      left: 26.5%
      max-width: 125px
      @media (min-width: 768px)
        top: 90%
        left: 21.5%
        max-width: 145px
      @media (min-width: 1024px)
        top: 87.5%
        left: 45%
        max-width: 170px
      @media (min-width: 1441px)
        display: none
    .product_image6
      display: none
      @media (min-width: 1024px)
        display: block
        position: absolute
        top: 37%
        left: 95%
        max-width: 120px
      @media (min-width: 1441px)
        max-width: 100%
        top: 31%
        left: 85%
    .product_image7
      display: none
      @media (min-width: 1024px)
        display: block
        top: 53%
        left: -10%
        position: absolute
        max-width: 140px
      @media (min-width: 1441px)
        max-width: 100%
        left: 3.5%
        top: 54%
  .btn-row
    margin-bottom: 30px
// main quality
.main-quality
  &__title
    margin-top: 6.75vh
    position: relative
    z-index: 2
    @media (min-width: 1024px)
      margin-top: 11.25vh
    @media (min-width: 1441px)
      margin-top: 8.25vh
    @media (min-width: 1200px)
      @media (max-height: 1000px)
        margin-top: 2.25vh
  .quality-list
    display: flex
    align-items: center
    justify-content: space-between
    position: absolute
    width: calc(100% - 30px)
    bottom: 30px
    left: 15px
    background: rgba(52, 182, 180, 0.5)
    backdrop-filter: blur(5px)
    border-radius: 10px
    padding: 10px
    z-index: 2
    @media (min-width: 768px)
      max-width: 360px
      left: 0
      right: 0
      margin: 0 auto
    @media (min-width: 1024px)
      position: relative
      top: 0
      bottom: 0
      left: 0
      max-width: 615px
      flex-wrap: wrap
      background: none
      backdrop-filter: none
      padding: 0
      border-radius: 0
      margin-top: 80px
    @media (min-width: 1441px)
      max-width: 1430px
      margin-top: 60px
    &__item
      flex: 0 0 auto
      width: auto
      margin: 0 auto
      position: relative
      @media (min-width: 1024px)
        display: flex
        align-items: center
        justify-content: flex-start
        flex: 0 0 calc(50% - 15px)
        max-width: calc(50% - 15px)
        margin: 0 20px 25px 0
        &:nth-of-type(2n)
          margin-right: 0
        &:nth-of-type(2)
          order: -1
          margin-right: 20px
        &:nth-of-type(1)
          margin-right: 0
      @media (min-width: 1441px)
        flex: 0 0 calc(25% - 25px)
        max-width: calc(25% - 25px)
        &:nth-of-type(2n)
          margin-right: 30px
        &:nth-of-type(4n)
          margin-right: 0
      .item-link
        display: block
        width: 100%
        height: 100%
        position: absolute
        top: 0
        left: 0
      .icon-block
        display: flex
        align-items: center
        justify-content: center
        .item-icon
          display: block
          width: auto
          margin: 0 auto
          max-width: 40px
          @media (min-width: 1024px)
            max-width: 100%
      .descr-block
        display: none
        @media (min-width: 1024px)
          display: block
          flex: 0 0 auto
          width: auto
          margin-left: 15px
          font-family: Lineatura, sans-serif
          font-weight: 700
          font-size: 16px
          line-height: 140%
          text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.4)
        @media (min-width: 1441px)
          font-size: 18px
  .section-content
    background: -moz-linear-gradient(top,  rgba(1,34,56,0) 0%, rgba(1,34,56,.5) 5%, rgba(1,34,56,1) 25%, rgba(1,34,56,1) 100%)
    background: -webkit-linear-gradient(top,  rgba(1,34,56,0) 0%,rgba(1,34,56,.5) 5%, rgba(1,34,56,1) 25%,rgba(1,34,56,1) 100%)
    background: linear-gradient(to bottom,  rgba(1,34,56,0) 0%,rgba(1,34,56,.5) 5%, rgba(1,34,56,1) 25%,rgba(1,34,56,1) 100%)
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00012238', endColorstr='#012238',GradientType=0 )
  &__content
    width: 100%
    flex: 1 1 100%
    height: auto
    .quality-slider
      max-height: var(--app-height)
      position: absolute
      bottom: 0
      left: 0
      width: 100%
      height: auto
      .swiper-slide
        .image-wrapper
          display: none
          &.mobile-image
            display: block
        @media (min-width: 421px)
          max-width: 390px
          margin-right: 15px
        @media (min-width: 768px)
          max-width: 470px
          .image-wrapper
            &.mobile-image
              display: none
            &.pad-image
              display: block
        @media (min-width: 1024px)
          max-width: 672px
          .image-wrapper
            &.pad-image
              display: none
            &.bpad-image
              display: block
        @media (min-width: 1441px)
          max-width: 870px
          .image-wrapper
            &.bpad-image
              display: none
            &.pc-image
              display: block
        .slide-image
          display: block
          width: auto
          height: 100%
          min-width: 100%
          object-fit: cover
      .swiper-pagination
        display: flex
        align-items: center
        justify-content: flex-start
        position: absolute
        bottom: 112px
        left: 15px
        width: 100%
        max-width: calc(100% - 30px)
        z-index: 2
        @media (min-width: 768px)
          max-width: 675px
          left: 0
          right: 0
          margin: 0 auto
        @media (min-width: 1024px)
          display: none
        .swiper-pagination-bullet
          flex: 1 1 auto
          width: 100%
          margin: 0 2px 0
          background-color: $neutral-color
          height: 3px
          border-radius: 5px
          opacity: 1 !important
          transition: background-color .5s, box-shadow .5s
          &.swiper-pagination-bullet-active
            background-color: $accent-color
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4)
  .btn-row
    position: relative
    z-index: 2
    margin-bottom: 145px
    @media (min-width: 1024px)
      margin-bottom: 30px
// main popular
.main-popular
  &__title
    margin-top: 6.75vh
    @media (min-width: 1024px)
      margin-top: 11.25vh
      br
        display: none
    @media (min-width: 1441px)
      margin-top: 8.25vh
    @media (min-width: 1200px)
      @media (max-height: 1000px)
        margin-top: 2.25vh
  .popular-list
    display: flex
    align-items: center
    justify-content: center
    margin-top: 25px
    @media (min-width: 768px)
      margin-top: 40px
    @media (min-width: 1024px)
      margin-top: 50px
    @media (max-height: 768px)
      margin-top: 35px
    &__item
      flex: 0 0 87px
      max-width: 87px
      text-align: center
      margin: 0 5px
      @media (min-width: 1024px)
        margin: 0 10px
        flex: 0 0 90px
        max-width: 90px
    &__link
      background-color: rgba(18, 197, 185, 0.2)
      color: $accent-color
      font-weight: 400
      font-size: 14px
      line-height: 150%
      display: block
      width: 100%
      text-align: center
      padding: 9px 0
      border-radius: 50%
      transition: background-color .3s, color .3s
      @media (min-width: 1024px)
        font-size: 16px
      &.active
        background-color: $accent-color
        color: #fff
  &__content
    width: 100%
    flex: 1 1 100%
    height: auto
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    margin: 30px 0
    .popular-slider
      display: none
      padding: 0 15px
      width: 100%
      @media (min-width: 768px)
        padding: 0 65px
      @media (min-width: 1024px)
        padding: 0 45px
      @media (min-width: 1441px)
        padding: 0 105px
      .swiper-slide
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        background-color: #fff
        border-radius: 10px
        padding: 10px
        max-width: 278px
        margin-right: 15px
        @media (min-width: 1024px)
          max-width: 372px
          padding: 15px
        @media (min-width: 1441px)
          max-width: 405px
        .image-block
          flex: 0 0 auto
          width: 100%
          overflow: hidden
          border-radius: 10px
          @media (max-width: 420px)
            @media (max-height: 700px)
              max-height: 150px
          @media (min-width: 1100px)
            @media (max-height: 768px)
              max-height: 170px
          .slide-image
            display: block
            min-width: 100%
            min-height: 100%
            margin: 0 auto
        .descr-block
          width: 100%
          display: flex
          align-items: flex-start
          justify-content: flex-start
          flex-direction: column
          flex: 1 1 auto
          padding: 10px 5px 5px
          @media (min-width: 768px)
            padding: 14px 5px 5px
          @media (min-width: 1024px)
            padding: 20px 15px
          .slide-title
            display: block
            font-family: Lineatura, sans-serif
            font-weight: 700
            font-size: 14px
            line-height: 130%
            color: #383838
            @media (min-width: 1024px)
              font-size: 18px
              line-height: 140%
          .slide-descr
            margin: 15px 0 10px
            font-size: 12px
            line-height: 150%
            color: #565656
            @media (min-width: 768px)
              margin: 12px 0 10px
            @media (min-width: 1024px)
              font-size: 14px
              line-height: 160%
              margin: 14px 0 12px
            @media (min-width: 1441px)
              font-size: 16px
              margin: 14px 0 14px
            @media (min-width: 1100px)
              @media (max-height: 768px)
                line-height: 120%
          .slide-btns
            width: 100%
            display: flex
            align-items: center
            justify-content: flex-start
            flex-wrap: wrap
            margin-top: auto
            .more-link
              flex: 0 0 34px
              width: 34px
              height: 34px
              display: flex
              align-items: center
              justify-content: center
              border-radius: 50%
              background-color: $accent-color
              color: #fff
              box-shadow: 0px 4px 6px rgba(0, 13, 21, 0.25)
              @media (min-width: 1024px)
                flex: 0 0 40px
                width: 40px
                height: 40px
              .icon
                font-size: 14px
            .old-price
              display: inline-block
              flex: 0 0 auto
              width: auto
              margin: 0 10px 0 22px
              font-weight: 400
              font-size: 14px
              line-height: 100%
              color: #7D7D7D
              position: relative
              @media (min-width: 1024px)
                font-size: 16px
              &:after
                content: ''
                display: block
                width: 100%
                height: 1px
                background-color: #7D7D7D
                position: absolute
                top: 50%
                left: 0
                transform: rotate(-15deg)
            .price
              display: inline-block
              flex: 0 0 auto
              width: auto
              margin: -7px 0 0 22px
              color: $accent-color
              font-family: Lineatura, sans-serif
              font-weight: 700
              font-size: 16px
              line-height: 100%
              @media (min-width: 1024px)
                font-size: 24px
                margin: -10px 0 0 18px
  .btn-row
    margin-bottom: 30px

// main fathers
.main-fathers
  // opacity for slider
  &.swiper-slide-active
    .main-fathers__content
      .fathers-slider
        opacity: 1
  &__title
    display: block
    position: absolute
    bottom: 115px
    left: 0
    font-family: Lineatura, sans-serif
    font-weight: 700
    font-size: 14px
    line-height: 130%
    text-transform: uppercase
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.4)
    padding: 20px 15px
    max-width: 300px
    z-index: 2
    overflow: hidden
    @media (min-width: 421px)
      max-width: 390px
      padding-right: 100px
      left: 0
      right: 0
      margin: 0 auto
    @media (min-width: 768px)
      max-width: 675px
      padding-right: 0
      bottom: 75px
    @media (min-width: 1024px)
      max-width: 800px
      position: relative
      bottom: 0
      margin-top: 11.25vh
      font-size: 40px
      line-height: 140%
      text-align: center
      padding: 0
      overflow: visible
      span
        color: $accent-color
    @media (min-width: 1441px)
      margin-top: 8.25vh
    @media (min-width: 1200px)
      @media (max-height: 1000px)
        margin-top: 2.25vh
  &__content
    position: relative
    flex: 1 1 100%
    height: 100%
    width: 100%
    display: flex
    align-items: center
    .fathers-slider
      opacity: 0
      transition: opacity 2.5s
      position: absolute
      bottom: 0
      left: 0
      width: 100%
      height: var(--app-height)
      @media (min-width: 1024px)
        padding: 0 47px
        position: relative
        height: auto
      @media (min-width: 1441px)
        padding: 0 107px
        max-height: calc(100% - 100px)
        margin: auto 0
      .swiper-slide
        height: auto
        @media (min-width: 1200px)
          @media (max-height: 1000px)
            max-height: 75vh
        @media (min-width: 1100px)
          @media (max-height: 768px)
            max-height: 65vh
        .image-wrapper
          display: none
          &.mobile-image
            display: block
        @media (min-width: 421px)
          max-width: 390px
          margin-right: 15px
        @media (min-width: 768px)
          max-width: 100%
          margin: 0
          .image-wrapper
            &.mobile-image
              display: none
            &.pad-image
              display: block
        @media (min-width: 1024px)
          max-width: 530px
          margin-right: 30px
          .image-wrapper
            &.pad-image
              display: none
            &.bpad-image
              display: block
        @media (min-width: 1441px)
          max-width: 693px
          .image-wrapper
            &.bpad-image
              display: none
            &.pc-image
              display: block
          .slide-image
            display: block
            width: 100%
            height: 100%
            min-width: 100%
            object-fit: cover
        .descr-block
          position: absolute
          width: 100%
          bottom: 0
          left: 0
          padding: 0 15px
          @media (min-width: 768px)
            max-width: 675px
            margin: 0 auto
            right: 0
          @media (min-width: 1024px)
            max-width: 100%
            padding: 45px 40px
          @media (min-width: 1441px)
            padding: 55px 50px
          .slide-title
            font-family: Lineatura, sans-serif
            font-weight: 700
            font-size: 30px
            line-height: 130%
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25)
            margin-bottom: 195px
            @media (min-width: 768px)
              margin-bottom: 155px
            @media (min-width: 1024px)
              margin-bottom: 0
              font-size: 24px

            span
              color: $accent-color
      .swiper-pagination
        display: flex
        align-items: center
        justify-content: flex-start
        position: absolute
        bottom: 90px
        left: 15px
        width: 100%
        max-width: calc(100% - 30px)
        z-index: 2
        @media (min-width: 768px)
          max-width: 675px
          bottom: 30px
          left: 0
          right: 0
          margin: 0 auto
        @media (min-width: 1024px)
          display: none
        .swiper-pagination-bullet
          flex: 1 1 auto
          width: 100%
          margin: 0 2px 0
          background-color: $neutral-color
          height: 3px
          border-radius: 5px
          opacity: 1 !important
          transition: background-color .5s, box-shadow .5s
          &.swiper-pagination-bullet-active
            background-color: $accent-color
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4)
// main lifehacks
.main-lifehacks
  &__title
    margin-top: 6.75vh
    @media (min-width: 1024px)
      margin-top: 11.25vh
    @media (min-width: 1441px)
      margin-top: 8.25vh
    @media (min-width: 1200px)
      @media (max-height: 1000px)
        margin-top: 2.25vh
  &__subtitle
    display: inline-block
    text-transform: uppercase
    font-family: Lineatura, sans-serif
    font-weight: 700
    font-size: 16px
    line-height: 150%
    text-transform: uppercase
    text-shadow: 0px 6px 4px rgba(0, 0, 0, 0.4)
    margin-top: 8px
    position: relative
    @media (min-width: 1024px)
      font-size: 20px
      margin-top: 25px
    @media (min-width: 1441px)
      font-size: 24px
    &:after
      content: url("../images/common/svg/arrow.svg")
      display: block
      position: absolute
      left: calc(100% + 40px)
      top: 3px
      pointer-events: none
      @media (min-width: 768px)
        left: calc(100% + 25px)
      @media (min-width: 1024px)
        top: 1.5vh
        left: calc(100% + 35px)
        transform: scale(1.25)
      @media (min-width: 1441px)
        top: 22px
        left: calc(100% + 45px)
  &__content
    margin: 5vh 0
    width: 100%
    @media (min-width: 1441px)
      margin: 7vh 0 0
    .lifehacks-slider
      padding: 20px 25px 10vh
      @media (min-width: 768px)
        padding: 20px 75px 10vh
      @media (min-width: 1024px)
        padding: 35px 60px 10vh
      @media (min-width: 1441px)
        padding: 38px 120px 10vh
      .swiper-slide
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        background-color: #fff
        border-radius: 10px
        padding: 10px
        max-width: 261px
        margin-right: 30px
        transition: transform .3s
        @media (min-width: 1024px)
          max-width: 370px
          padding: 15px
          margin-right: 55px
        @media (min-width: 1441px)
          max-width: 405px
          margin-right: 60px
        @media (max-height: 768px)
          max-width: 290px
        &:nth-of-type(2n+1)
          transform: rotate(3deg)
        &:nth-of-type(2n)
          transform: rotate(-3deg)
        .image-block
          flex: 0 0 auto
          width: 100%
          border-radius: 10px
          overflow: hidden
          @media (min-width: 1200px)
            @media (max-height: 1000px)
              max-height: 370px
          @media (min-width: 1100px)
            @media (max-height: 768px)
              max-height: 250px
          .slide-image
            display: block
            width: 100%
            height: 100%
            margin: 0 auto
            object-fit: cover
        .descr-block
          width: 100%
          display: flex
          align-items: flex-start
          justify-content: flex-start
          flex-direction: column
          flex: 1 1 auto
          padding: 12px 10px 8px
          @media (min-width: 1024px)
            padding: 15px 5px 10px
          @media (min-width: 1441px)
            padding: 15px 15px 20px
          .slide-title
            display: block
            font-family: Lineatura, sans-serif
            font-weight: 700
            font-size: 16px
            line-height: 150%
            color: $bg-color
            @media (min-width: 1024px)
              font-size: 18px
          .see-link
            display: inline-block
            font-weight: 400
            font-size: 14px
            line-height: 150%
            color: $bg-color
            position: relative
            margin: 5px 0 0 5px
            padding: 3px 0
            @media (min-width: 1024px)
              font-size: 16px
              margin: 15px 0 0 0
            @media (min-width: 1441px)
              margin: 20px 0 0 0
            @media (min-width: 1200px)
              @media (max-height: 1000px)
                margin: 10px 0 0 0
            &:before
              content: ''
              display: block
              width: 100%
              height: 1px
              background-color: $accent-color
              transition: background-color .3s
              position: absolute
              bottom: -2px
              left: 0
            &:hover
              &:before
                background-color: $bg-color
      .swiper-pagination
        display: flex
        align-items: center
        justify-content: flex-start
        position: absolute
        bottom: 0
        left: 15px
        width: 100%
        max-width: calc(100% - 30px)
        z-index: 2
        @media (min-width: 768px)
          max-width: 675px
          left: 0
          right: 0
          margin: 0 auto
        @media (min-width: 1024px)
          display: none
        .swiper-pagination-bullet
          flex: 1 1 auto
          width: 100%
          margin: 0 2px 0
          background-color: $neutral-color
          height: 3px
          border-radius: 5px
          opacity: 1 !important
          transition: background-color .5s, box-shadow .5s
          &.swiper-pagination-bullet-active
            background-color: $accent-color
            box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.4)
