    .steps-list
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      margin: 10vh auto 5vh
      @media (min-width: 1024px)
        flex-direction: row
        max-width: 930px
        margin: 14vh auto
      @media (min-width: 1441px)
        max-width: 1260px
      &__item
        display: flex
        align-items: center
        justify-content: flex-start
        margin-top: 56px
        @media (min-width: 768px)
          margin-top: 65px
        @media (min-width: 1024px)
          margin-top: 0
          align-items: flex-start
          flex: 0 0 calc(33.3% - 20px)
          max-width: calc(33.3% - 20px)
          margin-right: 30px
          position: relative
        &:nth-of-type(1)
          margin-top: 0
          @media (min-width: 1024px)
            &:before
              content: url("../images/common/svg/arrow_long.svg")
              display: block
              position: absolute
              top: -40px
              left: calc(50% + 20px)
          @media (min-width: 1441px)
            &:before
              transform: scale(1.3)
              top: -45px
              left: calc(50% + 50px)
        &:nth-of-type(2n + 1)
          .number
            &:after
              content: url("../images/common/svg/step_arrow1.svg")
              display: block
              position: absolute
              top: calc(100% + 2px)
              right: calc(100% + 6px)
              @media (min-width: 1024px)
                display: none
        &:nth-of-type(2n)
          @media (min-width: 1024px)
            &:before
              content: url("../images/common/svg/arrow_long.svg")
              display: block
              position: absolute
              bottom: -40px
              left: calc(50% + 20px)
              transform: scale(1,-1)
          @media (min-width: 1441px)
            &:before
              transform: scale(-1.3, -1.3)
              bottom: -45px
              left: calc(50% + 50px)
          .number
            &:after
              content: url("../images/common/svg/step_arrow2.svg")
              display: block
              position: absolute
              top: calc(100% + 8px)
              left: calc(100% - 12px)
              @media (min-width: 1024px)
                display: none
        &:last-child
          .number
            &:after
              display: none
        .number
          display: flex
          align-items: center
          justify-content: center
          flex: 0 0 60px
          width: 60px
          color: #fff
          font-family: Lineatura, sans-serif
          font-weight: 700
          font-size: 16px
          line-height: 100%
          padding: 6px 10px 16px
          text-align: center
          position: relative
          background-color: $accent-color
          box-shadow: 0px 8px 15px #035256
          border-radius: 50%
          @media (min-width: 1024px)
            flex: 0 0 70px
            width: 70px
            padding: 8px 0 18px
            font-size: 20px
          @media (min-width: 1441px)
            flex: 0 0 80px
            width: 80px
            padding: 6px 0 20px
            font-size: 24px
        p
          flex: 0 0 calc(100% - 60px)
          max-width: calc(100% - 60px)
          padding-left: 18px
          font-size: 14px
          line-height: 150%
          @media (min-width: 768px)
            text-transform: uppercase
