            max-width: calc(100% - 70px)
            text-transform: none
            padding-left: 22px
            line-height: 160%
          @media (min-width: 1441px)
            flex: 0 0 calc(100% - 80px)
            max-width: calc(100% - 80px)
            padding-left: 30px
            font-size: 16px
