.footer
  width: 100%
  margin-top: auto
  text-align: center
  font-size: 12px
  line-height: 150%
  padding: 25px 0
  @media (min-width: 768px)
    padding: 30px 0
  @media (min-width: 1024px)
    padding: 25px 5px
  .container
    @media (min-width: 1441px)
      max-width: 1790px
  &:after
    content: ""
    display: block
    width: calc(100% + 30px)
    height: 600px
    position: absolute
    top: calc(100% - 225px)
    left: 0
    background-image: url(../images/common/ancors/clouds.png)
    background-repeat: no-repeat
    background-size: 320%
    background-position: calc(35% - 15px) 0
    animation: fly_cloud 6s ease 0s infinite normal forwards
    z-index: -1
    pointer-events: none
    @media (min-width: 420px)
      background-size: cover
    @media (min-width: 1024px)
      height: 535px
      top: calc(100% - 205px)
      background-position: calc(40% - 15px) 0
    @media (min-width: 1441px)
      top: calc(100% - 325px)
  &__content
    position: relative
    z-index: 2
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    @media (min-width: 1024px)
      flex-direction: row
      align-items: center
      justify-content: flex-start
    @media (min-width: 1441px)
      max-width: 1710px
      margin: 0 auto
    &__column
      @media (min-width: 1024px)
        flex: 0 0 33.3%
        max-width: 33.3%
      &.copyright-column
        text-align: left
        @media (min-width: 1024px)
          order: -1
      &.social-column
        @media (min-width: 1024px)
          margin: 0 auto
        .social-list
          display: flex
          align-items: center
          justify-content: center
          margin: 20px 0
          &__item
            flex: 0 0 auto
            width: auto
            margin: 0 30px
            @media (min-width: 1024px)
              margin: 0 20px
            @media (min-width: 1441px)
              margin: 0 30px
          &__link
            display: inline-block
            color: $accent-color
            transition: transform .3s
            font-size: 16px
            line-height: 26px
            filter: drop-shadow(0px 6px 4px rgba(0, 0, 0, 0.4))
            @media (min-width: 1024px)
              font-size: 14px
            .icon
              &.icon-yt
                font-size: 26px
                @media (min-width: 1024px)
                  font-size: 24px
              &.icon-ok
                font-size: 24px
                @media (min-width: 1024px)
                  font-size: 22px
            &:hover
              transform: translateY(-5px)
      &.links-column
        text-align: right
        @media (min-width: 1024px)
          margin: 0 0 0 auto
        .doc-link
          display: inline-block
          margin-top: 10px
          color: #fff
          position: relative
          @media (min-width: 1024px)
            margin-top: 0
          &:before
            content: ''
            display: block
            width: 0
            height: 1px
            background-color: $accent-color
            transition: width .3s
            position: absolute
            bottom: 0
            left: 0
          &:hover
            &:before
              width: 100%